button {
	font-family: 'Pretendard-Regular';
	cursor: pointer;
}

.btn-wrapper {
	margin-left: 5vw;
	vertical-align: middle;
}

.btn, .btn2 {
    font-size: 2.5vw;
    font-weight: bold;
	width: 12.5vw;
	height: 12.5vw;
    color: white;
	text-align: center;
	vertical-align: middle;
	margin: 0.25vw 0.25vw;
}

.btn {
	border-style: solid;
	border-width: 0.75vw;
	border-color: #EA5550;
	background-color: #222A35;
}

.btn2 {
	border-style: none;
	background-color: #EA5550;

}

.btn:hover, .btn2:hover {
	background-color: #424953;
}

.btn-image-container {
	position: relative;
	width: 5vh;
	height: 5vh;
	border-style: none;
	background-color: initial;
}

.btn-image {
	position: absolute;
	width: 2vw;
	height: 2vh;
	top: 1vh;
	right: 7vw;
	transform: translate(-50%, -50%);
	transform: scale(0.2);
	border-style: none;
	background-color: initial;
}