* {
	margin: 0;
}

.FullPage {
	margin: 0;
}

mark {
	background-color: initial;
	color: #E9B43A;
}

a {
	text-decoration: none;
	color: white;
}

a:hover {
	text-decoration: none;
	color: #E9B43A;
}

.Number {
	margin: 4vh 4vh 4vh 4vh;
}

.Number-circle {
	background-repeat: no-repeat;
	background-size: 100%;
	height: 14vh;
	width: 14vh;
}

.Number-text {
	text-align: center;
	position: relative;
	top: 3.5vh;
	left: -0.2vh;
	font-size: 5vh;
}

.Video-source {
	display: flex;
	flex-direction: column;
}

.Video-source > iframe
{
	flex: 1 1 auto;
	height: 60vh;
}

.layoutView {
	height: 100vh;
	display: flex;
	flex-direction: column;
}

.title {
	flex-grow: 0;
}

.content {
	margin: 6vh 6vh 6vh 6vh;
	flex-grow: 1;
	font-size: 6vh;
}